<template>
  <v-data-table :headers="headers" :items="desserts" class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Les utilisateurs</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on"
              >Nouveau utilisateur
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="12">
                    <v-text-field
                    outlined
                      v-model="editedItem.nom"
                      label="Nom"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="12">
                    <v-text-field
                      v-model="editedItem.login"
                      label="Login"
                       outlined
                     :rules="emailRules"
                     required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="12">
                    <v-text-field
                      v-model="editedItem.mdp"
                      label="Mot de passe"
                      outlined
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="12">
                    <v-radio-group v-model="editedItem.role" label="Role">
                      <v-radio value="etudiant" label="Etudiant"></v-radio>
                      <v-radio
                        value="responsable"
                        label="Responsable"
                      ></v-radio>
                    </v-radio-group>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline"
              >Are you sure you want to delete this item?</v-card-title
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancel</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >OK</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.role="{ item }">
   <v-icon color="info">mdi-account </v-icon>
        {{ item.role }}
    </template>
    <template v-slot:item.mdp="{ item }">
<span v-for="(item, index) in item.mdp" :key="index">
*
</span>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize"> Reset </v-btn>
    </template>
  </v-data-table>
</template>
<script>
import Axios from "axios";
export default {
  data: () => ({
      
    emailRules: [
        (v) => !!v || "E-mail est obligatoire",
        (v) => /.+@.+\..+/.test(v) || "E-mail incorrect",
      ],
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: "role", value: "role" },
      { text: "Nom", value: "nom" },
      { text: "Login", value: "login" },
      { text: "mdp", value: "mdp", sortable: false },
      { text: "Actions", value: "actions" },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      id: "",
      id_etd: "",
      sujet: "",
      nom: "",
      login: "",
      mdp: "",
      role: "",
    },
    demandes: [],
    defaultItem: {
      id: "",
      id_etd: "",
      sujet: "",
      nom: "",
      login: "",
      mdp: "",
      role: "",
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      Axios.get("http://localhost:3000/user").then((res) => {
        res.data.forEach((item) => {
          if (localStorage.id) {
            this.desserts.push({
              id: item.id,
              nom: item.Nom,
              login: item.login,
              mdp: item.mdp,
              role: item.role,
            });
          }
        });
      });
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      Axios.put("http://localhost:3000/user/" + this.editedItem.id).then(() => {
        this.dialogDelete = true;
      });
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
        Axios.put("http://localhost:3000/user/" + this.editedItem.id, {
          id: this.editedItem.id,
          mdp: this.editedItem.mdp,
          login: this.editedItem.login,
          Nom: this.editedItem.nom,
          role: this.editedItem.role,
        }).then(() => {});
        this.close();
      } else {
        this.desserts.push(this.editedItem);
        Axios.post("http://localhost:3000/user/", {
          id: this.editedItem.id,
          mdp: this.editedItem.mdp,
          login: this.editedItem.login,
          Nom: this.editedItem.nom,
          role: this.editedItem.role,
        }).then(() => {});

        this.close();
      }
    },
  },
};
</script>